@import url('https://fonts.googleapis.com/css2?family=SF+Pro+Display:wght@400;700&display=swap'); /* Apple's San Francisco-inspired font */

:root {
    --primary: #ff4500; /* Fiery orange-red */
    --secondary: #2b2b2b; /* Dark charcoal for contrast */
    --secondary-light: #6b6b6b; /* Softer gray with better contrast for white text */
    --tertiary: #ff6347; /* Tomato red */
    --hover: #ff8c00; /* Dark orange */
    --text-color: #f5f5f5; /* Light gray for softer contrast */
    --font-nice: 'SF Pro Display', sans-serif;
}

html {
    -ms-overflow-style: none;
    /* Hide scrollbar for IE and Edge */
    scrollbar-width: none;

    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;

    user-drag: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;

    background-color: var(--secondary);
    font-family: var(--font-nice);
}

/* Hide scrollbar for Chrome, Safari and Opera */
html::-webkit-scrollbar {
    display: none;
}

body {
    margin-bottom: 60px;
    margin-left: 0px;
    margin-right: 0px;
    font-family: var(--font-nice); /* Apply the updated font */
}

h1 {
    color: var(--tertiary);
    text-align: center;
    margin: 0;
}

h2 {
    color: var(--tertiary);
}

p {
    color: var(--text-color);
    margin: 0;
}

img {
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    pointer-events: none;
}

.ErrorPage
{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 90vh;
}